import React, { Component } from "react";
import PacMan from "../loader/pacMan";
import axios from "axios";
import TextCounter from "../counters/textCounter";
import { config } from "../../mainConfig";

class AveragePerHour extends Component {
  constructor(props) {
    super(props);
    this.platform = this.props.platform;
    this.type = this.props.type;
    this.state = {
      data: null,
      loading: true,
      error: null,
    };
    this.getAveragePerHour = this.getAveragePerHour.bind(this);
  }

  componentDidMount() {
    this.getAveragePerHour();

    this.updateMetric = setInterval(
      this.getAveragePerHour,
      config.updateInterval
    );
  }

  componentWillUnmount() {
    clearInterval(this.updateMetric);
  }

  getAveragePerHour() {
    axios
      .get(`${config.apiUrl}/api/logs/import/average`, {
        params: {
          platform: this.platform,
          from: 30,
          type: this.type,
          interval: 3600, // One hour
        },
      })
      .then((response) => {
        const { avg } = response.data;
        this.setState({ data: avg, loading: false });
      })
      .catch(() => {
        return this.setState({ error: true, loading: false });
      });
  }

  render() {
    if (this.state.data && !this.state.error && !this.state.loading) {
      return <TextCounter data={this.state.data} text={this.props.text} />;
    } else if (this.state.error) {
      return (
        <p className="text-center text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
          An Error Occurred!
        </p>
      );
    } else {
      return <PacMan />;
    }
  }
}

export default AveragePerHour;
