import ImportMetrics from "./components/importMetrics";
import { platforms } from "./platformConfig";

const loadImporterMetrics = platforms.map((platform, index) => {
  return <ImportMetrics {...platform} key={platform.platform} />;
});

/* For future use.
const loadAIMetrics = platforms.map((platform, index) => {
  return <AIMetrics {...platform} key={platform.platform} />;
});*/
/*
   <div className="ai-metrics grid grid-cols-2 gap-x-5">
      {loadImporterMetrics}
    </div>
*/

const App = () => (
  <div className="p-5 mx-auto grid grid-cols-2 gap-y-5">
    <div className="import-logs">{loadImporterMetrics}</div>
  </div>
);

export default App;
