import React, { useState } from "react";
import Averages from "./averages";
import ImportedLineGraph from "./graphs/importedLineGraph";

function PlatformContent(props) {
  const [graphs] = useState([...props.graphs]);

  return graphs.map((graph, index) => {
    return (
      <div
        className="metric grid gap-y-5"
        key={graph.platform + graph.header + index}
      >
        <div className="metric-header grid grid-flow-col-dense items-center">
          <h2 className="text-left font-semibold text-gray-900 dark:text-gray-200 text-xl subpixel-antialiased">
            {graph.header}
          </h2>
          <Averages averages={graph.averages} platform={props.platform} />
        </div>
        <ImportedLineGraph
          platform={props.platform}
          color={props.color}
          type={graph.total.type}
          width={200}
          height={150}
        />
      </div>
    );
  });
}

export default PlatformContent;
